import React, { Component } from 'react';
import {Grid, Cell} from 'react-mdl';
import aboutImage from './img/IMG_3773.JPG';

class About extends Component {
    render () {
        return(
            <div style={{width: '80%', margin: 'auto'}}>
                <Grid className="about-grid">
                    <Cell col={12}>
                        <div className="about-title">About</div>
                    </Cell>
                    <Cell col={4}>
                        <img src={aboutImage} alt="aboutImage"  className="about-img"/>
                    </Cell>
                    <Cell col={8}>
                        <div className="banner-text"><h1>Michelle.</h1></div>
                        <div className="banner-text"><h1>Student.</h1></div>
                        <div className="banner-text"><h1>Traveler.</h1></div>
                        <div className="banner-text"><h1>Web Developer.</h1></div>
                    </Cell>
                    <Cell col={4}>
                        <div className="about-contact">
                            <h4>CONTACT : ackley05@email.franklin.edu</h4>
                            <a href="https://www.linkedin.com/in/michelle-ackley-b6984a121"><i class='fab fa-linkedin'></i></a>
                        </div>
                    </Cell>
                    <Cell col={8}>
                        <div className="about-content">
                            <h3>GOALS: Seeking a new project to further enhance my web development skills, and develop a strong foundation for my future career.</h3>
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default About;