import React, { Component } from 'react';
import {Grid, Cell } from 'react-mdl';
import Resume from './files/MichelleAckley_Resume.docx';
import projectsImage from './img/Final-Project-1.jpg';
import projectsImage2 from './img/favefoods.jpg';
import projectsImage3 from './img/carsnow.jpg';
import projectsPPT from './img/FinalPresentation.pptx';
import projectsPlan from './files/FinalProjectPlan.docx';


class Projects extends Component {
    render () {
        return(
            <div style={{width: '80%', margin: 'auto'}}>
                <Grid className="projects-grid">
                    <Cell col={12}>
                        <div className="projects-title">Work</div>
                    </Cell> 
                    <Cell col={6}>
                        <div className="projects-content">
                            <h3>This website was created using:</h3>
                            <i class='fab fa-react'><h4>React</h4></i>
                            <i class='fab fa-node-js'><h4>Node.js</h4></i>
                            <i class='fab fa-js'><h4>JavaScript</h4></i>
                            <i class='fab fa-html5'><h4>HTML5</h4></i>
                        </div>
                    </Cell>
                    <Cell col={6}>
                        <div className="projects-content">
                            <h3>Important Links:</h3>
                            <a href="https://www.linkedin.com/in/michelle-ackley-b6984a121"><i class='fab fa-linkedin'><h4>My Linkedin</h4></i></a>
                            <a href={Resume} alt="projectsResume" className="projects-content"><i class='far fa-file'><h4> My Resume</h4></i></a>
                            <a href="https://github.com/mackley22/REACTENV.git"><i class='fab fa-github'><h4>My GitHub</h4></i></a>  
                        </div>  
                    </Cell>
                    <Cell col={6}>
                        <div><img src={projectsImage} alt="projectsImage"  className="projects-img"/>
                        </div>
                    </Cell>
                    <Cell col={6}>
                        <div className="about-courses"><h3>Related Courses:</h3>
                            <ul class="fa-ul">
                                <li><i class="fa-li fa fa-check-square"></i>ADVANCED CLIENT-SIDE APPLICATIONS</li>
                                <li><i class="fa-li fa fa-check-square"></i>ADVANCED SERVER-SIDE DEVELOPMENT</li>
                                <li><i class="fa-li fa fa-check-square"></i>ADVANCED GRAPHIC DESIGN</li>
                                <li><i class="fa-li fa fa-check-square"></i>HUMAN-COMPUTER INTERACTION</li>
                                <li><i class="fa-li fa fa-check-square"></i>DATABASE MANAGEMENT SYSTEMSs</li>
                                <li><i class="fa-li fa fa-check-square"></i>WEB INFORMATION SYTEMS PROGRAMMINGs</li>
                                <li><i class="fa-li fa fa-check-square"></i>INFORMATION SYSTEMS ARCHITECTURE</li>
                            </ul>
                        </div>
                    </Cell>
                    <Cell col={6}>
                        <a href={projectsPPT} alt="projectsPPT" className="projects-ppt"><h4>Fave-Foods Application Plan</h4><img src={projectsImage2} className="project2" alt="project2"/></a>                            
                    </Cell>
                    <Cell col={6}>
                        <a href={projectsPlan} alt="projectsPlan" className="projects-plan"><h4>Cars Now Final Plan</h4><img src={projectsImage3} className="project3" alt="project3"/></a>                            
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Projects;